<template>
  <div class="template-container">
    <div class="template-item-wrap">
      <!-- 时间选择器 begin -->
      <div class="temp-item-inlineblock">
        <el-date-picker @change="pickerDateChange" v-model="pickerDate" type="daterange" align="left" unlink-panels range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerOptions2">
        </el-date-picker>
      </div>
      <!-- 时间选择器 end  -->
      <div class="temp-item-inlineblock">
        <div>自定义指标</div>
      </div>
      <div class="temp-item-inlineblock">
        <el-select v-model="configured_status_default" filterable placeholder="请选择" @change="changeConfiguredStatus">
          <el-option v-for="item in configured_status" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="temp-item-inlineblock">
        <el-input placeholder="请输入计划名查询" @change="changeCampaign_name"></el-input>
      </div>
      <div class="temp-item-inlineblock">
        <el-button type="primary" @click="getList">查询</el-button>
      </div>
      <div class="temp-item-inlineblock">
        <el-button type="primary" @click="exportExcel">导出为excel</el-button>
      </div>
      <div class="temp-item-inlineblock temp-item-right">
        <a class="new-plan" href="/#/advertising/campaign/new">新建计划</a>
      </div>
    </div>
    <div class="table-wrap">
      <el-table v-loading="loading" :data="listData" style="width: 100%" :default-sort="{prop: 'date', order: 'descending'}">
        <el-table-column prop="campaign_id" label="ID" width="60px" sortable>
        </el-table-column>
        <el-table-column prop="campaign_name" label="计划名称">
        </el-table-column>
        <el-table-column prop="impression" label="曝光量" width="100px" sortable>
        </el-table-column>
        <el-table-column prop="click" label="点击量" width="100px" sortable>
        </el-table-column>
        <el-table-column prop="click_rate" label="点击率" width="90px" sortable>
        </el-table-column>
        <el-table-column prop="cost_per_click" label="CPC" width="90px" sortable>
        </el-table-column>
        <el-table-column prop="cost" label="价格" width="90px" sortable>
        </el-table-column>
        <el-table-column prop="campaign_type_name" label="计划类型" width="120px" sortable>
        </el-table-column>
        <el-table-column prop="daily_budget" label="日限额(元)" width="90px" :formatter="unitConversion">
        </el-table-column>
        <el-table-column prop="budget_reach_date" label="日限额到达日期" sortable>
        </el-table-column>
        <el-table-column prop="created_time" label="创建时间" sortable>
        </el-table-column>
        <el-table-column prop="last_modified_time" label="修改时间" sortable>
        </el-table-column>
        <el-table-column prop="speed_mode_name" label="速度模式" width="100px" sortable>
        </el-table-column>
        <el-table-column prop="product_type_name" label="标的物类型" width="130px">
        </el-table-column>
        <el-table-column prop="configured_status" label="监控" width="60px">
          <template slot-scope="scope">
            <el-switch @change="handleListStatusChange(scope.row)" v-model="scope.row.configured_status === 'AD_STATUS_NORMAL' ? true : false" active-color="#13ce66" inactive-color="#999">
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column prop="configured_status" label="状态" width="60px">
          <template slot-scope="scope">
            <el-switch @change="handleListStatusChange(scope.row)" v-model="scope.row.configured_status === 'AD_STATUS_NORMAL' ? true : false" active-color="#13ce66" inactive-color="#999">
            </el-switch>
          </template>
        </el-table-column>
      </el-table>

      <div class="table-pagination" v-if="page_info.total_number>0">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="page_info.page" :page-sizes="[10, 20, 30, 40]" :page-size="page_info.total_page" layout="total, sizes, prev, pager, next, jumper" :total="page_info.total_number">
        </el-pagination>
      </div>
    </div>

  </div>

</template>

<script>
export default {
  created: function() {
    this.getAd_Campaigns(this.adCampaignsGet)
    this.getAdCommongetEnum()
  },
  data() {
    return {
      dialogVisible: false,
      dialogVisibleText: '',
      adCampaignsGet: {
        page: 1,
        page_size: 10,
        campaign_id: '',
        configured_status: '',
        campaign_name: '',
        start_date: '',
        end_date: ''
      },
      loading: true,
      pickerDate: '',
      pickerOptions2: {
        shortcuts: [
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
              picker.$emit('pick', [start, end])
            }
          }
        ]
      },
      configured_status: [],
      configured_status_default: '',
      listData: [],
      page_info: {
        page: 1,
        page_size: 10,
        total_number: 0,
        total_page: 0
      }
    }
  },
  computed: {},
  mounted() {
    //console.log(this.listData)
  },
  methods: {
    unitConversion(row, column) {
      return row.daily_budget / 100
    },
    //导出execel
    exportExcel() {
      var params = this.adCampaignsGet
      window.location.href =
        '/Admin/Marketing_Campaigns/export?page=' +
        params.page +
        '&page_size=' +
        params.page_size +
        '&campaign_id=' +
        params.campaign_id +
        '&configured_status=' +
        params.configured_status +
        '&campaign_name=' +
        params.campaign_name +
        '&start_date=' +
        params.start_date +
        '&end_date=' +
        params.end_date
    },
    getAdCommongetEnum() {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Common/getEnum', {})
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            //console.log('respData.list:', respData.data.list)
            var adStatus = respData.data['AD_STATUS']
            that.configured_status.push({ value: '', label: '所有状态' })
            for (var key in adStatus) {
              var obj = new Object()
              obj.value = key
              obj.label = adStatus[key]
              that.configured_status.push(obj)
            }
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    },
    changeConfiguredStatus(val) {
      this.adCampaignsGet.configured_status = val
    },
    changeCampaign_name(val) {
      this.adCampaignsGet.campaign_name = val
    },
    pickerDateChange() {
      var start_date = new Date(this.pickerDate[0]),
        end_date = new Date(this.pickerDate[1])
      this.adCampaignsGet.start_date =
        start_date.getFullYear() +
        '-' +
        (start_date.getMonth() + 1) +
        '-' +
        start_date.getDate()
      this.adCampaignsGet.end_date =
        end_date.getFullYear() +
        '-' +
        (end_date.getMonth() + 1) +
        '-' +
        end_date.getDate()
    },
    handleListStatusChange(row) {
      var current_status = row.configured_status
      if (row.configured_status === 'AD_STATUS_NORMAL') {
        row.configured_status = 'AD_STATUS_SUSPEND'
      } else {
        row.configured_status = 'AD_STATUS_NORMAL'
      }
      var that = this
      this.$axios
        .get('/Admin/Marketing_Campaigns/update', {
          params: {
            campaign_id: row.campaign_id,
            configured_status: row.configured_status
          }
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code != 0) {
            that.$message.error(respData.msg)
            row.configured_status = current_status
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          row.configured_status = current_status
        })
    },
    handleSizeChange(val) {
      this.adCampaignsGet.page_size = val
      this.getAd_Campaigns(this.adCampaignsGet)
      //console.log(`每页 ${val} 条`)
    },
    handleCurrentChange(val) {
      this.adCampaignsGet.page = val
      this.getAd_Campaigns(this.adCampaignsGet)
      //console.log(`当前页: ${val}`)
    },
    getList() {
      this.getAd_Campaigns(this.adCampaignsGet)
    },
    // 推广计划 -- 列表请求
    getAd_Campaigns: function(adParams) {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Campaigns/get', {
          params: adParams
        })
        .then(function(response) {
          //console.log(response)
          that.loading = false
          var respData = response.data
          if (respData.code == 0) {
            //console.log('respData.list:', respData.data.list)
            if (respData.data.list || respData.data.page_info) {
              that.listData = respData.data.list
              that.page_info = respData.data.page_info
            }
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          that.loading = false
        })
    }
  }
}
</script>


<style scoped>
.new-plan {
  text-decoration: none;
  display: inline-block;
  color: #fff;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #409eff;
  border: 1px solid #409eff;
  -webkit-appearance: none;
  text-align: center;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: 0;
  margin: 0;
  -webkit-transition: 0.1s;
  transition: 0.1s;
  font-weight: 500;
  padding: 12px 20px;
  font-size: 14px;
  border-radius: 4px;
}
</style>



